import Head from "next/head";
import styles from "./step-2.module.css";
import classNames from "classnames";
import Image from "next/image";
import gifProps from "../../public/assets/ios_step2_en.gif";
import IOSStep from "@/components/ios-step";
import { base_url } from "@/utils/const";
import EnableExtention from "@/components/enable-extention";
import Wechat from "@/components/wechat";
import { useI18n, useLang } from "@/hooks";

const prevUrl = base_url + "/ios/step-1/";
const nextUrl = base_url + "/ios/step-3/";

type LangType = {
  title: string,
  prev: string,
  next: string
}
const langs: { [key: string]: LangType } = {
  default: {
    title: "",
    prev: "",
    next: ""
  },
  zh: {
    title: "沉浸式翻译 - 启用扩展",
    prev: "上一步",
    next: "我已完成，下一步"
  },
  en: {
    title: "Immersive Translation - Enable Extension",
    prev: "Previous step",
    next: "I'm done, next step"
  }
}
export default function Step2() {
  const i18n = useI18n<LangType>(langs);
  return (
    <main>
      <Head>
        <title>{i18n["title"]}</title>
        <meta name="viewport" content={"width=device-width"} />
      </Head>
      <EnableExtention />
      <div className={classNames("card mt-2", styles.container)}>
        <IOSStep step={2} />
        <div className={classNames("loading", styles.imageWapper)}>
          <ViewGif />
        </div>
        <footer className={classNames("notranslate", styles.footer)}>
          <a
            id="prev"
            href={prevUrl}
            className={classNames(styles.secondary, "underline")}
          >
            {i18n["prev"]}
          </a>
          <link rel="prefetch" href={prevUrl} />
          <a className={classNames("ghost")} rel="noreferrer" href={nextUrl}>
            {i18n["next"]}
          </a>
          <link rel="prefetch" href={nextUrl} />
        </footer>
      </div>
      <Wechat />
    </main>
  );
}
function ViewGif() {
  const lang = useLang();
  if (!lang) return null;
  return <Image
    src={`/assets/ios_step2_${lang}.gif`}
    width={gifProps.width}
    height={gifProps.height}
    alt=""
  />
}